import Vue from 'vue';
import Router from 'vue-router';

const Login = () => import('@/components/login');
const Admin = () => import('@/components/admin');
const Index = () => import('@/components/module/index');

const Pay = () => import('@/components/pay');


Vue.use(Router);

export default new Router({
	// mode:'history',
	// base:'parking2',
  routes: [
  	{
  		path: '/login',
  		name: 'login',
  		component: Login
		},
		{
  		path: '/pay',
  		name: 'pay',
  		component: Pay
  	},
    {
    	path: '/',
    	component: Admin,
    	children: [
    		{
    			path: '',
    			name: 'index',
    			component: Index
				}
    	]
    }
  ]
});



