export default {
	state: {
		isLogin: false
	},
	mutations: {
		getUserInfoSuccess (state, isLogin) {
      state.isLogin = isLogin;
    },
    getUserInfoFailed (state) {
      state.isLogin = false;
    },
	},
	actions: {
		// 获取用户信息来判断有没有登录
    getUserInfo({commit}, params){
    	return new Promise((resolve, reject) => {
    		this._vm.ajax({
				  method: 'post',
				  url: '/parking2/getuserinfo',
				  data: {
				  }
				}).then(function (response) {
					// let isLogin = response.data.data.isLogin;
			    // commit('getUserInfoSuccess', isLogin);
			    resolve(response);
			  })
			  .catch(function (error) {
			    commit('getUserInfoFailed', params);
			  });
      })
    	
    },
	},
	getters: {
		isLogin: (state) => state.isLogin,
	}
};


